//fonts
@mixin font($alias, $name) {
   @font-face {
      font-family: $alias;
      src: url("../fonts/" + $name + ".eot");
      src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".ttf") format("truetype");
      font-weight: normal;
      font-style: normal; } }

@include font('MuseoSansCyrl-300', 'MuseoSansCyrl-300');
@include font('MuseoSansCyrl-500', 'MuseoSansCyrl-500');
@include font('MuseoSansCyrl-700', 'MuseoSansCyrl-700');
@include font('MuseoSansCyrl-900', 'MuseoSansCyrl-900');

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
   $font-family: null;
   @if $name == "ms300" {
      $font-family: "MuseoSansCyrl-300"; }
   @if $name == "ms500" {
      $font-family: "MuseoSansCyrl-500"; }
   @if $name == "ms700" {
      $font-family: "MuseoSansCyrl-700"; }
   @if $name == "ms900" {
      $font-family: "MuseoSansCyrl-900"; }
   font-family: $font-family, Arial, sans-serif;
   @if $font-family != null {
      font-weight: normal; }
   @if $font-size != null {
      font-size: $font-size * 1px; }
   @if $line-height != null {
      line-height: $line-height * 1px; }
   @if $letter-spacing != null {
      letter-spacing: ($letter-spacing / 1000) * 1em; } }
