body {
  position: relative;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "MuseoSansCyrl-300", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.2;
  color: #5a5f62;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
  }
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container {
  position: relative;
  z-index: 2;
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.section-title {
  position: relative;
  padding-bottom: 15px;
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 48px;
  line-height: 58px;
}

.section-title::after {
  position: absolute;
  bottom: 0;
  width: 100px;
  height: 3px;
  border-radius: 2px;
  content: '';
}

.section-title_white {
  color: #fff;
}

.section-title_white::after {
  background-color: #00bff3;
  background-image: linear-gradient(to right, #acd4fe 0%, #e6f2ff 100%);
}

.section-title_black {
  color: #333333;
}

.section-title_black::after {
  background-color: #00bff3;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
}

.section-title_blue {
  color: #1c9bee;
}

.section-title_blue span {
  color: #333333;
}

.section-title_blue::after {
  background-color: #00bff3;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
}

.section-title_center {
  text-align: center;
}

.section-title_center::after {
  left: calc(50% - 50px);
}

.section-title_left {
  text-align: left;
}

.section-title_left::after {
  left: 0;
}

.section-title_left-center {
  padding-right: 120px;
  text-align: left;
}

.section-title_left-center::after {
  right: 0;
  bottom: 50%;
  margin-bottom: 2px;
  content: '';
}

.section-title_full-white {
  color: #fff;
}

.section-title span {
  font-family: "MuseoSansCyrl-300", Arial, sans-serif;
  font-weight: normal;
}

.section-title_white .section-title span {
  color: #fff;
}

.section-title_blue .section-title span {
  color: #1c9bee;
}

.title-lg {
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 58px;
  line-height: 62px;
  color: #fff;
}

.title-sm {
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  color: #1c9bee;
}

.title-sm span {
  color: #333333;
  font-family: "MuseoSansCyrl-500", Arial, sans-serif;
  font-weight: normal;
}

.btn {
  position: relative;
  display: inline-block;
  padding: 19px 30px;
  outline: 0;
  border-radius: 50px;
  transition: .3s ease-in-out;
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #fff;
  text-shadow: 0 2px 1px rgba(31, 23, 3, 0.18);
  box-shadow: 0 16px 15px rgba(6, 44, 69, 0.13);
}

.btn_yellow {
  border-radius: 35px;
  border: 1px solid #f6db2c;
  background-color: #fff;
  background-image: linear-gradient(to top, #f5ac0f 0%, #ffeb3e 100%);
}

.btn_blue {
  border-radius: 35px;
  border: 1px solid #71cbff;
  background-color: #ffffff;
  background-image: linear-gradient(to top, #1799ed 0%, #59bffa 100%);
}

.btn_fluid {
  width: 100%;
}

.btn_md {
  min-width: 400px;
}

.dashed-link {
  font-family: "MuseoSansCyrl-300", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 0.05em;
  color: #5c6164;
  border-bottom: 1px dashed rgba(34, 144, 202, 0.7);
  transition: .3s ease-in-out;
}

.dashed-link:hover {
  border-bottom-color: transparent;
}

.circle-decor {
  position: absolute;
  pointer-events: none;
}

.has-grad-bg {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.has-grad-bg::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(to right, #1c9bed, #57bef9);
  opacity: .8;
  pointer-events: none;
  content: '';
}

.has-striped-bg {
  position: relative;
  background-image: linear-gradient(to top, #e3ebf6 0%, #f0f6f9 100%);
}

.has-striped-bg::after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-image: repeating-linear-gradient(45deg, transparent, #fbfbfb 1px, #fbfbfb 1px, #fbfbfb 4px);
  content: '';
}

.call-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.call-block__tel {
  display: flex;
  align-items: center;
  font-size: 22px;
  color: #333333;
}

.call-block__tel span {
  margin-left: 4px;
  color: #1c9bee;
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
}

.call-block__tel-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  background: #7f898e linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  border-radius: 50%;
  color: #fff;
  font-size: 16px;
}

.header {
  padding: 20px 0;
  border-top: 3px solid #1c9bee;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__title {
  flex: 0 1 240px;
}

.header__title span {
  color: #1c9bee;
  font-family: "MuseoSansCyrl-900", Arial, sans-serif;
  font-weight: normal;
}

@media only screen and (max-width: 767px) {
  .nav {
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 130px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    padding-bottom: 130px;
    overflow: auto;
    opacity: 0;
    border: 0;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #fff;
    text-align: center;
    box-shadow: inset 0 0 10px rgba(28, 155, 238, 0.5);
  }
}

.nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  margin-right: 14px;
}

@media only screen and (max-width: 1023px) {
  .nav__list {
    margin-right: 0;
    padding-bottom: 4px;
  }
}

@media only screen and (max-width: 767px) {
  .nav__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
    height: 100%;
    padding-bottom: 0;
  }
}

.nav__list li {
  position: relative;
}

.nav__list li:not(:last-child) {
  margin-right: 30px;
}

@media only screen and (max-width: 767px) {
  .nav__list li:not(:last-child) {
    margin-bottom: 30px;
  }
}

.nav__list li:not(:last-child)::after {
  position: absolute;
  top: 50%;
  right: -15px;
  width: 4px;
  height: 4px;
  margin-top: -2px;
  border-radius: 50%;
  background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
  content: '';
}

.nav__list li a {
  display: block;
  font-size: 15px;
  line-height: 1;
