// *** begin screen *** //
.screen {
   padding: 80px 0;
   //&::after
   // //opacity: .8
   &__container {
      display: flex;
      align-items: center; }
   &__title {
      margin-bottom: 20px; }
   &__content {
      flex: 0 0 825px; }
   &__img {
      position: absolute;
      top: -100px;
      right: -30px;
      z-index: 1;
      pointer-events: none; }
   &__form {
      &-inner {
         margin-bottom: 30px;
         padding: 30px 31px;
         background: #fff;
         box-shadow: 0 8px 20px rgba(#063553, .13);
         border-radius: 17px; }
      &-title {
         margin-bottom: 10px; }
      &-row {
         margin: 0 -8px; }
      &-field {
         flex: 0 1 calc(50% - 16px);
         margin: 0 8px; } }
   &__circle-img {
      position: absolute;
      z-index: 1;
      pointer-events: none;
      &_l {
         top: -30%;
         left: 0; }
      &_r {
         right: 0;
         top: -30%; } } }
// end screen //

// *** begin promo *** //
.promo {
   padding: 50px 0;
   &__container {
      display: flex;
      align-items: flex-start;
      justify-content: space-between; }
   &__img {
      flex: 0 1 percentage(445 / 1110); }
   &__text {
      flex: 0 1 percentage(665 / 1110);
      padding-left: 40px; }
   &__title {
      margin-bottom: 20px; }
   &__descr {
      color: $color6;
      p {
         &:not(:last-child) {
            margin-bottom: 15px; } } } }
// end promo //

// *** begin services *** //
.services {
   padding: 60px 0;
   overflow: hidden;
   &__container {}
   &__title {
      margin-bottom: 30px; }
   &__row {
      margin: 0 -30px; }
   &__item {
      flex: 0 1 33.33333%;
      padding: 0 30px;
      //max-width: 390px
      &:nth-child(3) ~ & {
         padding: 30px 30px 0; }

      &-top {
         position: relative;
         padding: 15px 20px;
         margin-bottom: 30px;
         box-shadow: 0 0 38px rgba(145, 155, 167, 0.1);
         border-radius: 17px;
         background-color: #ffffff; }
      &-img-wrap {
         position: relative;
         height: 190px;
         width: 136px;
         margin: 0 auto;
         @include cover-img;
         img {
            &:nth-child(2) {
               position: absolute;
               left: calc(100% - 77px);
               bottom: 17px;
               width: 154px;
               height: 108px;
               box-shadow: 0 4px 9px rgba(10, 50, 77, 0.15); } } }
      &-label {
         color: #fff;
         @include f('ms700', 14);
         &-wrap {
            position: absolute;
            top: 0;
            right: 0;
            width: 83px;
            height: 83px;
            @include flexCenter;
            background-color: #c01a41;
            background-image: linear-gradient(to top, rgba(192, 26, 65, 0.98) 0%, rgba(213, 29, 72, 0.98) 100%);
            border-radius: 50%;
            &::after {
               position: absolute;
               top: -4px;
               left: -4px;
               right: -4px;
               bottom: -4px;
               border: 1px solid #c01a41;
               opacity: 0.5;
               border-radius: 50%;
               content: ''; } } }
      &-info {
         display: flex;
         align-items: center;
         margin-bottom: 30px; }
      &-price {
         display: flex;
         flex-direction: column;
         align-items: center;
         color: #515151;
         @include f('ms300', 18);
         span {
            margin-bottom: 5px;
            @include f('ms900', 32, 20);
            color: $color1; }
         &-wrap {
            @include flexCenter;
            flex: 0 0 140px;
            width: 140px;
            height: 140px;
            background: url("../img/decor/price-circle.svg");
            @include cover; } }
      &-descr {
         flex: 0 1 auto;
         padding-left: 20px;
         p {
            color: $color6;
            font-size: 15px; } }
      &-title {
         margin-bottom: 5px;
         @include f('ms900', 20);
         color: $color2; } }

   &__circle-decor {
      position: absolute;
      top: 70%;
      right: -30%; } }
// end services //

// *** begin pros *** //
.pros {
   padding: 60px 0;
   &::after {
      opacity: .75; }
   &__title {
      text-align: center;
      margin-bottom: 30px; }
   &__container {}

   &__row {}
   &__item {
      flex: 0 0 calc(25% - 30px);
      margin: 0 30px;
      padding: 25px 10px;
      min-height: 240px;
      box-shadow: 0 16px 15px rgba(3, 14, 27, 0.1);
      border-radius: 17px;
      background-color: #ffffff;
      text-align: center;
      &-icon {
         margin: 0 auto 30px; }
      &-content {
         position: relative;
         padding: 25px 0 0;
         &::before {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            border-top: 1px solid rgba(#00bff3, .2);
            content: ''; } }
      &-title {
         margin-bottom: 5px;
         @include f('ms900', 24, 20);
         color: $color1; }
      &-descr {
         line-height: 24px;
         color: $color2; } }
   &__bg-img {
      position: absolute;
      top: 0;
      height: 100%;
      &_l {
         left: 0; }
      &_r {
         right: 0; } } }
// end pros //

// *** begin reasons *** //
.reasons {
   position: relative;
   padding: 60px 0;
   overflow: hidden;
   &__container {}
   &__title {
      margin-bottom: 40px; }

   &__row {
      align-items: center;
      margin: 0; }

   &__col {
      $parent: &;
      flex: 0 1 percentage(350 / 1110); }
   &__center {
      position: relative;
      flex: 0 1 percentage(410 / 1110);
      &::after {
         @include vcenter;
         width: 100.5%;
         height: 87%;
         background: url("../img/other/eclipse.png") no-repeat 50% 50% / cover;
         content: ''; }
      &-img {
         width: percentage(297 / 410);
         margin: 0 auto;
         @include ratio(297, 354);
         @include cover; } }
   &__item {
      $parent: &;
      position: relative;
      &:not(:last-child) {
         padding-bottom: 20px; }
      &_l {
         padding-right: 20px;
         text-align: right;
         &:nth-child(2) {
            margin-right: 30px; } }
      &_r {
         padding-left: 20px;
         text-align: left;
         &:nth-child(2) {
            margin-left: 30px; } }
      &-circle {
         position: absolute;
         top: 13px;
         #{$parent}_l & {
            right: 0; }
         #{$parent}_r & {
            left: 0; }
         &::before, &::after {
            @include vcenter;
            border-radius: 50%;
            content: ''; }
         &::before {
            width: 15px;
            height: 15px;
            border: 1px solid #2fa6f1;
            opacity: 0.5; }
         &::after {
            width: 9px;
            height: 9px;
            background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%); } }
      &-title {
         margin-bottom: 10px;
         @include f('ms900', 22);
         color: $color1; }
      &-descr {} }

   &__bg-img {
      position: absolute;
      &_l {
         left: 0;
         bottom: 0;
         width: 614px;
         height: 368px; }
      &_r {
         right: -20%;
         bottom: -4px;
         width: 903px;
         height: 602px; } } }
// end reasons //

// *** begin delivery *** //
.delivery {
   padding: 60px 0;
   &__container {
      @include clr; }
   &__bg-img {
      position: absolute;
      top: -30px;
      left: 50%;
      margin-left: -680px;
      bottom: 0;
      width: 510px;
      z-index: 3; }
   &__content {
      float: right;
      width: percentage(730 / 1110); }
   &__title {
      margin-bottom: 30px; }
   &__info {
      margin-bottom: 20px;
      padding: 20px 30px;
      box-shadow: 0 8px 20px rgba(6, 53, 83, 0.13);
      border-radius: 17px;
      border: 3px solid #ffffff;
      background-color: #ffffff;
      &-text {
         margin-bottom: 20px; }
      &-tel {
         @include f('ms900', 32);
         color: $color4; } } }
// end delivery //

// *** begin work *** //
.work {
   padding: 60px 0;
   &__container {
      max-width: 1010px; }
   &__title {
      margin-bottom: 30px; }
   &__row {
      flex-wrap: wrap;
      margin: 80px -15px 0; }
   &__item {
      flex: 0 1 33.33333%;
      width: 33.33333%;
      padding: 0 25px;
      text-align: center;
      &:nth-child(3) ~ & {
         padding: 40px 15px 0; }

      &-top {
         position: relative;
         @include flexCenter;
         margin: 0 auto 30px;
         width: 140px;
         height: 140px;
         background: #fff url("../img/decor/work-item_eclipse.svg") no-repeat 50% 100% / auto;
         border-radius: 50%; }
      &-count {
         position: absolute;
         top: -20px;
         left: 50%;
         transform: translateX(-50%);
         color: $color1;
         @include f('ms900', 36, 36);
         letter-spacing: 0.9px; }
      &-icon {
         margin: 0 auto; }
      &-title {
         margin-bottom: 10px;
         @include f('ms900', 20);
         color: $color2; } } }
// end work //

// *** begin guarantees *** //
.guarantees {
   padding: 60px 0;
   height: 615px;
   @include cover;
   &__content {
      max-width: 825px; }
   &__title {
      max-width: 530px;
      margin-bottom: 30px; }
   &__row {
      margin: 0 -15px 30px; }
   &__item {
      flex: 0 1 calc(33.33333% - 30px);
      width: calc(33.33333% - 30px);
      margin: 0 15px;
      padding: 20px 21px;
      box-shadow: 0 16px 15px rgba(6, 44, 69, 0.13);
      border-radius: 17px;
      border: 1px solid #71cbff;
      background-color: #ffffff;
      text-align: center;
      &-icon {
         &-wrap {
            position: relative;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            height: 100px;
            margin-bottom: 15px;
            padding-bottom: 15px;
            &::after {
               position: absolute;
               left: 0;
               width: 100%;
               bottom: 0;
               height: 1px;
               border-radius: 1px;
               background-color: #8bcded;
               opacity: 0.3;
               content: ''; } } }
      &-title {
         margin-bottom: 5px;
         @include f('ms900', 24, 20);
         color: $color1; }
      &-descr {
         color: $color2; } }
   &__bg-img {
      pointer-events: none;
      position: absolute;
      top: -105px;
      right: -70px;
      width: 534px;
      height: 640px; } }
// end guarantees //

// *** begin testimonials *** //
.testimonials {
   padding: 60px 0;
   &__title {
      margin-bottom: 40px; }
   &__slider {
      .slick-list {
         margin: 0 -15px; } }
   &__item {
      padding: 50px 15px 0;
      &-content {
         position: relative;
         margin-bottom: 30px;
         padding: 70px 30px 20px;
         border-radius: 17px 17px 17px 0;
         background: #fff;
         &::after {
            @include arr(19, 19, #fff, tr);
            content: ''; } }
      &-img {
         &-wrap {
            width: 94px;
            height: 94px;
            margin-top: -120px;
            border: 3px solid #f7bc1b;
            border-radius: 50%;
            overflow: hidden;
            @include cover-img; } }
      &-title {
         color: #fff;
         @include f('ms900', 22, 20);
         span {
            @include f('ms300'); } } } }

// end testimonials //
