.nav {
   @include xs {
      flex-direction: column;
      justify-content: center;
      position: absolute;
      top: 130px;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 10;
      padding-bottom: 130px;
      overflow: auto;
      opacity: 0;
      border: 0;
      visibility: hidden;
      transform: translate3d(0, 100px, 0);
      transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
      background: #fff;
      text-align: center;
      box-shadow: inset 0 0 10px rgba($color1, .5); }
   &.is-active {
      opacity: 1;
      visibility: visible;
      transform: translate3d(0, 0, 0);
      transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease; }
   &__list {
      display: flex;
      justify-content: space-between;
      margin-right: 14px;
      @include sm {
         margin-right: 0;
         padding-bottom: 4px; }
      @include xs {
         flex-direction: column;
         justify-content: center;
         align-items: center;
         overflow: auto;
         height: 100%;
         padding-bottom: 0; }
      li {
         position: relative;
         &:not(:last-child) {
            margin-right: 30px;
            @include xs {
               margin-bottom: 30px; }
            &::after {
               position: absolute;
               top: 50%;
               right: -15px;
               width: 4px;
               height: 4px;
               margin-top: -2px;
               border-radius: 50%;
               background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
               content: ''; } }
         a {
            display: block;
            font-size: 15px;
            line-height: 1;
            color: #5c6164;
            border-bottom: 1px dashed transparent;
            transition: .3s ease-in-out;
            &:hover {
               color: $color1;
               border-bottom-color: rgba(#2290ca, .7); } } } } }

.nav-btn {
   $parent: &;
   display: none;
   position: relative;
   top: -5px;
   border: 0;
   outline: 0;
   padding: 0;
   z-index: 11;
   @include xs {
      display: block; }

   &.is-active span {
      color: $color1; }

   &-burger {
      display: block;
      margin: auto;
      width: 40px;
      height: 40px;
      cursor: pointer; }
   &-lines {
      display: block;
      width: 40px;
      height: 3px;
      &:after, &:before {
         width: 40px;
         height: 3px; }
      position: relative;
      transform: translateY(25px);
      background: #fff;
      transition: all .3s;
      #{$parent}.is-active & {
         background: rgba(255, 255, 255, 0); }
      &:before {
         content: "";
         position: absolute;
         left: 0;
         bottom: 10px;
         background: #fff;
         transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
      &:after {
         content: "";
         position: absolute;
         left: 0;
         top: 10px;
         background: #fff;
         transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
      #{$parent}.is-active & {
         &:after {
            top: 0;
            transform: rotate(45deg);
            transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
         &:before {
            bottom: 0;
            transform: rotate(-45deg);
            transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); } } } }
